<template>
  <section class="main">
    <h1 class="intro">{{ $t("MYAPPSTORE_HEADING") }} (<a href="https://www.youtube.com/watch?v=5g6-JeZff8I" target="_blank">{{ $t('VIDEO_TUTORIAL') }}</a>)</h1>
    <div class="row">
      <div>
        <p class="g-text">
          {{ $t("MYAPPSTORE_DESC") }}
          <b class="email"
            >Email ID:
            {{ $store.getters.devices[0] && $store.getters.devices[0].email }}
          </b>
        </p>
        <div class="header">
          <h2 class="header__title">{{ $t("YOUR APPS") }}</h2>
          <span class="header__storage"
            >{{ $t("STORAGE USAGE") }}: {{ totalSize }} / {{ currentStorageLimit }} GB<br>
            <html-with-links
              v-if="!hasPlus"
              tag="span"
              style="font-size: 12px;"
              :html="'(' + $t('UPGRADE_MY_APPSTORE_STORAGE',
                {storage: stats.myappstore_storage_limit_gb_plus + ' GB', href: '/my/plus'}) + ')'"
              />
          </span>
        </div>
        <app
          v-for="app in apps"
          :app="app"
          @update="getApps"
          :key="app.id"
          class="main__app"
          :isInStore="true"
        />
      </div>
      <div class="upload">
        <form class="upload__box">
          <input
            @change="newFilesHandler"
            class="upload__input"
            ref="upload"
            type="file"
            multiple
          />
          <v-button type="primary" @click.native.prevent="select" class="upload__button">
            {{ $t("UPLOAD_IPA_FILES") }}
          </v-button>
          <v-button
            @click.native.prevent="uploadByUrl"
            :loading="uploadingByUrl"
            type="primary"
            class="upload__button"
          >{{ $t("UPLOAD_BY_URL") }}</v-button>
          <span v-if="uploads.length" class="upload__num">
            {{ $t("FILES_SELECTED", { count: uploads.length }) }}
          </span>
          <ul>
            <li v-for="upload in uploads" :key="upload.id" class="upload__item">
              <template v-if="!upload.status">
                <span class="upload__title">{{ upload.name }}</span>
                <span class="upload__progress-wrapper">
                  <span class="upload__progress">
                    <span
                      :style="{ width: upload.progress + '%' }"
                      class="upload__progress-inner"
                    ></span>
                  </span>
                  {{ upload.progress }}%
                </span>
              </template>
              <template v-else>
                <span
                  class="upload__status"
                  :class="{ upload__status_error: upload.error }"
                >
                  {{ upload.status }}
                </span>
                <span class="upload__date">
                  {{ upload.date }}
                  <img
                    v-if="upload.finished"
                    @click="deleteJob(upload.id)"
                    class="upload__delete"
                    src="@/assets/icon-delete.svg"
                  />
                </span>
              </template>
            </li>
          </ul>
        </form>
        <p class="help g-text">
          <img class="help__icon" src="@/assets/icon-info.svg" />
          <html-with-links
            tag="span"
            class="help__text"
            :html="
              $t('CONFIGURE_FEATURES_AT_PAGE', {
                configureLink: '/my/configure',
              })
            "
          ></html-with-links>
        </p>
      </div>
    </div>
  </section>
</template>

<script>
import App from '@/components/App';
import prettysize from 'prettysize';
import HtmlWithLinks from '@/components/HtmlWithLinks';
import VButton from '@/components/Button';
import { mapGetters } from 'vuex';

export default {
  components: { App, HtmlWithLinks, VButton },
  data() {
    return {
      apps: [],
      stats: {},
      uploads: [],
      uploadingByUrl: false,
    };
  },
  computed: {
    ...mapGetters(['hasPlus']),
    title() {
      return this.$t('MYAPPSTORE_HEADING');
    },
    totalSize() {
      return prettysize(this.apps.reduce((total, a) => total + (+a.size), 0));
    },
    currentStorageLimit() {
      return this.hasPlus ? this.stats.myappstore_storage_limit_gb_plus
        : this.stats.myappstore_storage_limit_gb;
    },
  },
  methods: {
    select() {
      this.$refs.upload.click();
    },
    generateId() {
      return [...Array(40)].map(() => Math.random().toString(36)[2]).join('');
    },
    newFilesHandler() {
      Array.from(this.$refs.upload.files).forEach((file) => {
        const id = this.generateId();
        const app = {
          id,
          name: file.name,
          progress: 0,
          status: '',
          date: '',
          finished: false,
          error: false,
        };
        this.uploads.unshift(app);
        this.$api.uploadApp({ file, id }, (e) => {
          app.progress = Math.round((e.loaded * 100) / e.total);
        }).then((response) => {
          if (!response.success) {
            app.status = this.$t('UPLOAD_FAILED_WITH_REASON', { reason: response.errors[0].translated });
            app.error = true;
          } else {
            app.status = this.$t('REQUESTING_VALIDATION');
          }
        }).catch(() => {
          app.status = this.$t('UPLOAD_FAILED');
          app.error = true;
        });
      });
    },
    uploadByUrl() {
      this.$prompt(this.$t('PLEASE_PROVIDE_URL_TO_IPA'))
        .then((url) => {
          this.uploadingByUrl = true;
          return this.$api.uploadAppByUrl({ url, id: this.generateId() });
        })
        .finally(() => {
          this.uploadingByUrl = false;
        });
    },
    getApps() {
      this.$api.getIpas().then((apps) => {
        this.apps = apps;
      });
    },
    deleteJob(id) {
      this.$api.deleteAppJob(id).then(() => {
        const index = this.uploads.findIndex(u => u.id === id);
        this.uploads.splice(index, 1);
      });
    },
    getJobs() {
      this.$api.getAppsJobs().then((jobs) => {
        jobs.forEach((newJob) => {
          const oldJob = this.uploads.find(u => u.id === newJob.id);
          const date = new Date(newJob.updated_at * 1000).toLocaleString();
          const error = /error/i.test(newJob.status);

          if (!oldJob) {
            this.uploads.push({
              id: newJob.id, status: newJob.status, date, finished: newJob.is_finished, error,
            });
            return;
          }

          oldJob.status = newJob.status;
          oldJob.date = date;
          oldJob.error = error;
          if (newJob.is_finished && !oldJob.finished) {
            oldJob.finished = true;
            this.getApps();
          }
        });
      });
    },
    async scheduleGetApps() {
      // eslint-disable-next-line no-underscore-dangle
      if (this._isDestroyed) {
        return;
      }

      try {
        await this.getApps();
      } finally {
        setTimeout(() => {
          this.scheduleGetApps();
        }, 5000);
      }
    },
    async scheduleGetJobs() {
      // eslint-disable-next-line no-underscore-dangle
      if (this._isDestroyed) {
        return;
      }

      try {
        await this.getJobs();
      } finally {
        setTimeout(() => {
          this.scheduleGetJobs();
        }, 1000);
      }
    },
  },
  mounted() {
    this.scheduleGetApps();
    this.scheduleGetJobs();

    this.$api.getStats().then((response) => {
      this.stats = response;
    });
  },
};
</script>

